#sidebarCollapsed {
  min-width: 50px;
  max-width: 50px;
  background: rgb(255, 248, 233);
  transition: all 0.3s;
  height: 100vh;
  margin-left: -50px;
  position: fixed;
}

#sidebarCollapsed.active {
  margin-left: 0px;
}

.svgButton {
  border-radius: 30% 0% 0% 30%;
  border: 0px;
  background-color: rgb(255, 248, 233);
  padding: 5%;
}

.svgButton:hover svg {
  color: rgb(32, 31, 29);
  transition: all 0.3s;
}

.svgButton:active svg {
  color: rgba(0, 0, 0, 0.3);
  transition: all 0.3s;
}

.svgButton.active {
  background-color: rgb(251, 240, 217);
  transition: all 0.3s;
}/*# sourceMappingURL=SideBarCollapsed.module.css.map */